.modal-form {
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(black, .5);
	display: none; }

.modal-form__item {
	background-color: #fff;
	border-radius: 10px;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	max-width: 1000px;
	z-index: 5000;
	text-align: center;
	padding: 50px; }

.modal-form__close {
	position: absolute;
	top: 80px;
	right: 80px;
	width: 20px;
	height: 20px;
	background-image: url("../img/modal-close.png");
	cursor: pointer; }

.modal-form__overlay {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	z-index: 4999;
	width: 100%;
	height: 100%; }
